#about {
    .about-container-div {
        padding: 40px;
        display: flex;
        position: relative;
        .about-details {
            margin-right: 2rem;
            width: 60%;

            .about-title, .about-description {
                line-height: 3rem;
            }
        }

        .about-me-img {
            width: 40%;
            box-shadow: 0px -1px 4px gray;
            object-fit: cover; //почитать
            object-position: 0 0;
            max-height: 50vh;
        }

        .download-link {
            margin-top: 1.5rem;
            display: inline-block; //почитать
            @include btn-style;
        }

        @media (min-width: 850px) {
            .download-link {
                position: absolute;
                bottom: 40px;
                left: 40px;
            }
        }

        @media (max-width: 850px) {
            flex-direction: column-reverse;
            .about-details {
                margin-right: 0;
                width: 100%;
            }

            .about-me-img {
                width: 40vh;
                height: 40vh;
                margin: 0 auto 2rem;

                border-radius: 250px;
            }
        }
    }
}